* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Fira Sans, sans-serif;
    font-size: 18px;
    font-weight: 400;
    color: #282626;
    @media (max-width: 425px) {
        font-size: 16px;
    }
}

a {
    text-decoration: none;
    color: inherit;
}